<!--suppress HtmlUnknownTag, HtmlUnknownAttribute -->
<template>
  <div>
    <h3>Out-of-Context Hillwalkers' Quotes</h3>
    <P>
      A pseudo-random collection of the funniest and most accidentally spoken words and adages by our club members are here for your enjoyment:
    </P>

    <v-card>
      <v-card-text v-if="this.loadError" style="color:red">
        Error!
      </v-card-text>
      <v-card-text v-else-if="quoteVisible" style="min-height:250px">
        <span v-for="line in this.quotes[this.current]">{{ line }}<br></span>
      </v-card-text>

      <v-card-actions>
        <v-btn :disabled="this.loadError"
               :loading="this.loading"
               @click="nextQuote()">
          {{ buttonText }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import axios from 'axios';

function swap (arr, i, j) {
  let temp = arr[i];
  arr[i] = arr[j];
  arr[j] = temp;
}

function shuffleBatches (data) {
  for (let i=0; i < data.length; i++)
    for (let j=0; j < data[i].length; j++)
      swap (data[i],
        Math.floor(Math.random() * data[i].length),
        Math.floor(Math.random() * data[i].length));
}

function flattenIntoModel(data, model) {
  for (let i = data.length-1; i >= 0; i--)
    for (let j=0; j < data[i].length; j++)
      model.push(data[i][j]);
}

function nextQuote () {
  this.current = (this.current + 1) % this.quotes.length;
  this.blankOrMore = ' more';
}

export default {
    name: 'SUHC_Quotes',

    data: () => ({
      loading: true,
      loadError: false,
      blankOrMore: '',
      current: -1, // Ugh hacks
      quotes: [],
      nextQuote
    }),

    computed: {
      quoteVisible () {
        return this.current >= 0;
      },

      buttonText () {
        return this.quoteVisible ? 'More' : 'Show me!';
      }
    },

    // TODO Axios helper
    mounted () { axios({ method: 'GET', url: this.$dataUrl + '/suhc/quotes.json' })
      .then(result => { /* console.log('Result data', result.data); */
                        shuffleBatches(result.data)
                        flattenIntoModel(result.data, this.quotes);
                        this.loading = false;
                      },
            error  => { console.error(error);
                        this.loading = false;
                        this.loadError = true;
                      })}
  }

</script>
